import { Component, OnInit, ViewChild } from '@angular/core';
import { Location } from '@angular/common';
import { CartService } from 'src/app/services/cart.service';
import { ActivatedRoute, Router } from '@angular/router';
import { FoodDrinkFood, OrderItem } from 'src/app/objects/food-drink-menu.model';
import { AccountService } from 'src/app/services/account.service';
import { AuthService } from 'src/app/services/auth.service';
import { Address,Account } from 'src/app/objects/account.model';
import { CityTown } from 'src/app/objects/city-town.model';
import firebase from 'firebase';
import { CartItem, Order, OrderStatus } from 'src/app/objects/cart.model';
import { AlertController } from '@ionic/angular';
import { ChannelService } from 'src/app/services/channel.service';
import { Channel, ShippingType } from 'src/app/objects/channel.model';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.page.html',
  styleUrls: ['./cart.page.scss'],
})
export class CartPage implements OnInit {
  cid:string;
  cartItems:CartItem[]; 
  iller:any=[];
  ilceler: any[];
  addresses: Address[];
  show_address_box:boolean = false;
  show_table_box:boolean = false;
  editedAddress: Address;
  shippingAddress: Address;
  account: Account;
  @ViewChild('content') private content: any;
  channel:Channel;
  table_name:string;
  name_surname:string;
  toTableAddress: Address;
  order_success:boolean= false;

  constructor(public location:Location,
              private alertController:AlertController,
              public router: Router,
              public route: ActivatedRoute,
              public authService:AuthService,
              private accountService:AccountService,
              private channelService:ChannelService,
              private cartService:CartService) { }

  async ngOnInit() {     
    if (this.router.getCurrentNavigation().extras.state){
      this.cid  = this.router.getCurrentNavigation().extras.state.cid
      await this.channelService.getChannelInfo(this.cid).ref.get().then((channel)=>{
        this.channel = channel.data()
      })
    }

    this.getCart() 

    if(this.channel.shipping_type   == <ShippingType>'TO_ADDRESS'){
      this.show_address_box         = true
      this.iller                    = (new CityTown()).getCityes();
      this.getAddresses();
      this.getAccount();
    }
    
    if(this.channel.shipping_type   == <ShippingType>'TO_TABLE'){
      this.toTableAddress           = new Address();
      this.show_table_box           = true;
      this.showInputTableNum();
      //BURADA KALDIN
    }
  }

  showInputTableNum(){}

  getAccount(){
    this.accountService.getAccount(this.authService.USER.uid).then(async (data)=>{
      if(data){  
        this.account = data;
      }else{
        alert("Hesap bilgilerine erişilemedi")
        this.location.back();
      }
    })
  }

  getCart(){
    this.cartService.getCart(this.cid,this.channel).valueChanges({ idField: 'id' }).subscribe(data=>{ 
      this.cartItems = data;
      data.map(item=>{
        item.food.orders.map((order:OrderItem)=>{

        })
      })
      return data.length;
    })
  }

  getFoodTotal(food:FoodDrinkFood){
    let total:number        = 0;
    food.orders.map(orderitem=>{
        total += orderitem.additional.quantity*orderitem.additional.price
    })
    return food.price+total;
  }

  getAdditionalPrice(forders){
    let total:number        = 0;
    forders.map(orderitem=>{
        total += orderitem.additional.quantity*orderitem.additional.price
    })
    return total;
  }

  totalAmount(){
    let total_amount = 0
    this.cartItems?.map(item=>{
      total_amount += this.getFoodTotal(item.food) 
    })
    return total_amount
  }

  rejectFood(item:any){ 
    this.cartService.rejectFood(item,this.cid)
  }

  getAddresses(){
    let uid = this.authService.USER.uid;
    this.accountService.getAddresses(uid).valueChanges({idField:'id'}).subscribe(async (datas)=>{
      this.addresses = datas.map(doc=>{ 
        return {id:doc.id,...doc}
      })
    })
  }

  saveAdress(){

    let error:boolean = false;
    document.getElementById('name').style.border        = "1px solid #ccc";
    document.getElementById('adi_soyadi').style.border  = "1px solid #ccc";
    document.getElementById('tel').style.border         = "1px solid #ccc";
    document.getElementById('mahalle').style.border     = "1px solid #ccc";
    document.getElementById('cadde_sokak').style.border = "1px solid #ccc";
    document.getElementById('bina').style.border = "1px solid #ccc";

    if(this.editedAddress.name?.trim() =='' || this.editedAddress.name === undefined){
      document.getElementById('name').style.border = "1px solid red";
      error = true
    }
    
    if(this.editedAddress.adi_soyadi?.trim() == '' || this.editedAddress.adi_soyadi === undefined){
      document.getElementById('adi_soyadi').style.border = "1px solid red";
      error = true
    }
    
    if(this.editedAddress.tel?.trim() == '' || this.editedAddress.tel === undefined){
      document.getElementById('tel').style.border = "1px solid red";
      error = true
    }
    
    if(this.editedAddress.mahalle?.trim() == '' || this.editedAddress.mahalle === undefined){
      document.getElementById('mahalle').style.border = "1px solid red";
      error = true
    }
    
    if(this.editedAddress.cadde_sokak?.trim() == '' || this.editedAddress.cadde_sokak === undefined){
      document.getElementById('cadde_sokak').style.border = "1px solid red";
      error = true
    }
    
    if(this.editedAddress.bina?.trim() == '' || this.editedAddress.bina === undefined){
      document.getElementById('bina').style.border = "1px solid red";
      error = true
    }

    if(error){return;}

    let uid = this.authService.USER.uid;
    this.accountService.saveAddress(uid,this.editedAddress) 
    this.shippingAddress = this.editedAddress;
  }

  deleteAdress(){
    let uid = this.authService.USER.uid;
    this.accountService.deleteAdress(uid,this.editedAddress).then(()=>{
      if(this.addresses[0]){
        this.switchAddress(this.addresses[0])
      }else{
        this.newAdress(); 
      }
    })

  }

  switchAddress(address:Address){ 

    try { 
      document.getElementById('name').style.border        = "1px solid #ccc";
      document.getElementById('adi_soyadi').style.border  = "1px solid #ccc";
      document.getElementById('tel').style.border         = "1px solid #ccc";
      document.getElementById('mahalle').style.border     = "1px solid #ccc";
      document.getElementById('cadde_sokak').style.border = "1px solid #ccc";
      document.getElementById('bina').style.border        = "1px solid #ccc";
    }catch (error) { }

    this.editedAddress    = address
    this.shippingAddress  = address
    this.ilceler  = (new CityTown()).getTowns(this.editedAddress.city['id']);
    let yOffset = document.getElementById("address-anchor").offsetTop ;
    this.content.scrollToPoint(0, yOffset , 1000)
  }

  newAdress(){
    this.editedAddress = new Address();
    this.shippingAddress = null;
  }

  changeCtity(event:any){
    this.ilceler  = (new CityTown()).getTowns(this.editedAddress.city['id']);    
    this.iller.map(il=>{
      if(event.target.value == il.id){
        this.editedAddress.il_adi  = il.name;
      } 
    })
  }

  changeTown(event:any){  
    this.ilceler.map(ilce=>{
      if(event.target.value == ilce.id){
        this.editedAddress.ilce_adi  = ilce.name;
      } 
    }) 
  }

  complateOrder(){ 
    if(this.channel.shipping_type == <ShippingType>'TO_ADDRESS'){
      this.complateWithAddress();
    }else{
      this.complateWithTable()
    }
    
  }

  async complateWithTable() {
    let error:boolean = false;
    document.getElementById('name_surname_for_totable').style.border = '1px solid #ccc'
    document.getElementById('table_name_for_totable').style.border = '1px solid #ccc'
    if(this.toTableAddress.name_surname_for_totable?.replace(/\s/g,'').length <= 3 
      || this.toTableAddress.name_surname_for_totable === undefined){
        document.getElementById('name_surname_for_totable').style.border = '1px solid red'
        error = true
    }
    if(!this.toTableAddress.table_name_for_totable?.replace(/\s/g,'').length 
      || this.toTableAddress.table_name_for_totable === undefined){
        document.getElementById('table_name_for_totable').style.border = '1px solid red'
        error = true
    }

    if(error){
      const alert = await this.alertController.create({
        cssClass: 'my-custom-class',
        header  : 'Dikkat !',
        message : 'Masanızı ve isminizi yazmadınız',
        buttons : [{
              text: 'Tamam',
              handler: async () => {
              }
            }
          ]
        });
        await alert.present();
        return
    }

    let order               = new Order() 
    order.uid               = window.localStorage.getItem('uid') 
    order.shipping_address  = JSON.parse(JSON.stringify(this.toTableAddress))
    order.created_at        = firebase.firestore.FieldValue.serverTimestamp() 
    order.cart_items        = this.cartItems;
    order.status            = {status:(new OrderStatus()).status[0].id,created_at:order.created_at,cancel_by_cutomer:''}

 
    this.cartService.complateOrder(order,this.cid).then(()=>{
      this.cartItems.forEach((item:any) => { 
        this.rejectFood(item)
      });
    })
    this.order_success = true;
  }

  async complateWithAddress(){
    let address_error:boolean = false
    if(!this.shippingAddress){
      const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Dikkat !',
      message: 'Teslimat adresinizi seçmediniz',
      buttons: [{
            text: 'Tamam',
            handler: async () => {
            }
          }
        ]
      });
      await alert.present();
      return 
    }

    if(this.shippingAddress.name?.length<3 || this.shippingAddress.name === undefined){
      document.getElementById('name').style.borderBottom = "1px solid red"
      address_error = true; 
    }
    if(this.shippingAddress.adi_soyadi?.length<3 || this.shippingAddress.adi_soyadi === undefined){
      document.getElementById('adi_soyadi').style.borderBottom = "1px solid red"
      address_error = true;
    }
    if(this.shippingAddress.mahalle?.length<3 || this.shippingAddress.mahalle === undefined){
      document.getElementById('mahalle').style.borderBottom = "1px solid red"
      address_error = true;
    }
    if(this.shippingAddress.cadde_sokak?.length<3 || this.shippingAddress.cadde_sokak === undefined){
      document.getElementById('cadde_sokak').style.borderBottom = "1px solid red"
      address_error = true;
    }
    if(this.shippingAddress.bina?.length<3 || this.shippingAddress.bina === undefined){
      document.getElementById('bina').style.borderBottom = "1px solid red"
      address_error = true;
    }
    if(this.shippingAddress.tel?.length<3 || this.shippingAddress.tel === undefined){
      document.getElementById('tel').style.borderBottom = "1px solid red"
      address_error = true;
    }

    if(address_error){ 
      let yOffset = document.getElementById("address-anchor").offsetTop ;
      this.content.scrollToPoint(0, yOffset + 100, 1000)
      return
    }
      
    let order               = new Order()
    order.uid               = this.authService.USER.uid;
    order.account           = this.account;
    order.shipping_address  = JSON.parse(JSON.stringify(this.shippingAddress))
    order.created_at        = firebase.firestore.FieldValue.serverTimestamp() 
    order.cart_items        = this.cartItems;
    order.status            = {status:(new OrderStatus()).status[0].id,created_at:order.created_at,cancel_by_cutomer:null}
    this.cartService.complateOrder(order,this.cid).then(()=>{
      this.cartItems.forEach((item:any) => {
        this.rejectFood(item)
      });
    })
    this.order_success = true;
  }

  back(){
    this.location.back();
  }
}